button {
  /* width: 75px;
    height: 35px; */

  border: solid 1px transparent;
  border-radius: 5px;
  background-image: linear-gradient(white, white),
    linear-gradient(150deg, #0500ff, #bd00ff);
  background-origin: border-box;
  background-clip: content-box, border-box;
  box-shadow: 5px 5px 4px rgb(5 0 255 / 25%);

  font-size: 20px;
  transition: all 0.1s;
}

.content_button {
  background: linear-gradient(
    to right bottom,
    #0500ff 0%,
    #bd00ff 50%,
    #bd00ff 100%
  );
  background-clip: text;
  color: transparent;
  font-family: "Syncopate", sans-serif;
  font-size: 1rem;
  margin: 10px;
}

button:hover {
  transform: translate(5px, 5px);
  box-shadow: 0px 0px 0px rgb(5 0 255 / 25%);
}

/* dark mode */

.black button {
  /* width: 75px;
    height: 35px; */

  border: solid 1px transparent;
  border-radius: 5px;
  background-image: linear-gradient(#0c0923, #0c0923),
    linear-gradient(150deg, #ff0000, #ffb800);
  background-origin: border-box;
  background-clip: content-box, border-box;
  box-shadow: 5px 5px 4px rgb(255 184 0 / 25%);

  font-size: 20px;
  transition: all 0.1s;
}

.black button:hover {
  transform: translate(5px, 5px);
  box-shadow: 0px 0px 0px rgb(255 184 0 / 25%);
}

.black .content_button {
  background: linear-gradient(
    to right bottom,
    #ff0000 0%,
    #ffb800 50%,
    #ffb800 100%
  );
  background-clip: text;
  color: transparent;
}
