@import url("https://fonts.googleapis.com/css2?family=Darker+Grotesque:wght@300&family=Syncopate&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.aside {
  width: 10vw;
  height: 100vh;
  position: fixed;
  left: 0;
}

.contain {
  width: 80vw;
  height: 100vh;
  margin-left: 10vh;
}

.menu {
  width: 10vw;
  height: 100vh;
}

p,
a,
button {
  font-family: "Darker Grotesque", sans-serif;
}
p {
  font-size: 22px;
}

a {
  text-decoration: none;
}

@media (max-width: 768px) {
  .App {
    flex-direction: column;
  }

  h1 {
    font-size: 2rem;
  }
}

.black {
  background-color: #0c0923;
  transition: background 500ms;
}
.white {
  background-color: #fff;
  transition: background 500ms;
}
